.encounter,
.encounter-totaldps,
.encounter-duration {
  display: none;
}
.encounter-totaldps.show,
.encounter-duration.show,
.encounter.show {
  display: block;
}

.encounter {
  text-shadow: 0 0 8px #000;
  text-align: center;
  margin-top: 4px;
}
.encounter > div {
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  width: 280px;
  -webkit-transform: skew(-30deg);
      -ms-transform: skew(-30deg);
          transform: skew(-30deg);
  background: rgba(255, 255, 255, 0.1);
  padding: 0px 10px;
}
.encounter > div > div {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;

  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  -webkit-transform: skew(30deg);
      -ms-transform: skew(30deg);
          transform: skew(30deg);
  margin: 2px 4px;
}



